/* Skills section */
.skills {
  background-color: rgb(110, 110, 110);
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.custom-slider .slick-prev {
  color: black; /* Changes the color of the arrow */
  background: black; /* If you want to change the background */
}
.custom-slider .slick-next {
  color: black; /* Changes the color of the arrow */
  background: black; /* If you want to change the background */
}

.custom-slider .slick-next:before .slick-prev:before{
  color: black; /* Changes the color of the arrow icon specifically */
}

.title {
  font-size: 4rem;
  color: black;
  margin: 3rem 0;
  text-align: center;
  margin-top: 5%;
  letter-spacing: 0.3rem;
}

.skill-cards {
  background-color: gray;
  padding: 1.2rem 1rem 1rem 1rem;
  margin: 1.2rem;
    width: 10rem;
  border-radius: 8px;
  color: #ffffff;
}

.skill-icon {
  color: white;
  font-size: 40px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.skill-holder {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-evenly;
  align-items: center;
  margin: 2em 0;
}

.skill {
  font-size: 1.3rem;
  font-weight: bolder;
  text-align: center;
  text-shadow: 2px 3px 4px #000000;
  text-transform: uppercase;
}

.skill-cards:hover {
  box-shadow: 0px 2px 10px #000000;
}

@media (max-width: 500px) { 
