  } 
} 
/* End Skills section */

/* Footer section  */
.footer {
    min-height: 20vh;
    width: 100%;
    background-color: black;
    color: white;
    flex-direction: column;
    text-align: center;
    padding: 5rem;
}
.footer .footer-contact-info {
    padding: 20px;
}
.footer .footer-contact-info .footer-heading {
    font-size: 2.5rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 10px;
}
.footer .footer-contact-info .footer-contact-access {
    font-size: 1.4rem;
    padding-top: 10px;
    letter-spacing: .2rem;
  margin-bottom: -0.1rem;
}
.footer .social-icons a {
    color: white;
    font-size: 3rem;
    margin: 0.7rem;
    display: inline-block;
}
/* End Footer section  */

.cube {
  z-index: -1;
  position:fixed; /* position the component absolutely within the parent element */
  left: 50%; /* align the left edge of the component with the horizontal center of the parent element */
  top: 50%; /* align the top edge of the component with the vertical center of the parent element */
  transform: translate(-50%, -50%); /* move the component back by half its width and height to center it within the parent element */
  overflow: hidden; /* hide the content of the component when it overflows its bounds */
}

.raindrop {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: blue;
  border-radius: 50%;
}
.rain-scene {
  position: absolute;
  width: 100%;
  height: 100%;

}

.random-letter {
  display: inline-block;
  width: 20px;
  height: 20px;
  color: rgb(87, 87, 87);
  /* background: rgba(0, 255, 55, 0.285); */
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
}
.random-letter-grid {
  position: absolute;
  z-index: 1;
}

/* Auth Forms */
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--primary-color);
  padding: 1rem;
}

.auth-form-box {
  background: white;
  border-radius: 8px;
  padding: 2rem;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.auth-form-box h2 {
  margin-bottom: 1.5rem;
  color: var(--secondary-color);
  text-align: center;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--secondary-color);
}

.form-group input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.auth-button {
  width: 100%;
  padding: 0.75rem;
  background-color: var(--secondary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.auth-button:hover {
  background-color: var(--accent-color);
}

.auth-links {
  margin-top: 1.5rem;
  text-align: center;
}

.auth-links a {
  color: var(--secondary-color);
  text-decoration: underline;
}

.error-message {
  color: var(--accent-color);
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 480px) {
  .auth-form-box {
    padding: 1.5rem;
  }
}

